import React from "react";
import PropTypes from "prop-types";
import OraSwitcher from "eshop/modules/core/components/ui/OraSwitcher";


export default class OraSwitcherSelect extends React.Component {

    constructor(props) {
        super(props);
        // this.oraSwitcherClassLGroup = "l-group__element u-padding-m u-padding-right u-small-no-padding-bottom"
    }

    handleClick(event) {
        const {id, value} = event.currentTarget.dataset;
        this.props.onChange({id, value});
    }

    componentDidMount() {
        if (this.ref) {
            OPL.UI.loadModules(this.ref, this.oplLayoutFixerModule());
            this.updateDimensions()
        }
    }

    componentDidUpdate() {
        if (this.ref) {

            OPL.UI.loadModules(this.ref, this.oplLayoutFixerModule());
            this.updateDimensions()
        }
    }

    oplLayoutFixerModule() {
        return (
            {
                path: "core/modules/layout-fixer",
                options: {
                    selectors: [
                        ".js-layout-fixer-group-switcher-select" + this.props.id,
                    ],
                },
            }
        )
    }

    updateDimensions() {
        if (this.ref)
            OPL.UI.fire(OPL.UI.EVENTS.modules.layoutFixer.recalculate, this.ref);
    }

    options() {
        // grid layout
        return this.props.options.map((option, index) => this.switcher(option, index))

    }
    isDisabled(option){
        console.log("isDisabled",this.props.disabledOptions && !!this.props.disabledOptions.find(opt=> opt.value==option.value));
        console.log(option);
        console.log(this.props.disabledOptions);
        if(this.props.disabledOptions && !!this.props.disabledOptions.find(opt=> opt.value==option.value))
            return true;
        return false;
    }

    switcher(option, index) {
        let oraSwitcherClass = "opl-flex-boxes__item";
        let switcherClassName = "u-position-relative u-margin-m u-large-no-margin";
        let disabled=this.isDisabled(option);
        return (
            <OraSwitcher
                key={this.props.id + "_" + option.value + "_" + index}
                className={oraSwitcherClass}
                dataId={this.props.id + option.value}
                dataValue={option.value}
                onChangeHandler={!disabled&&this.handleClick.bind(this)}
                switcherClassName={"switcher " + switcherClassName}
                isSelected={option.value === this.props.selected}>
                <div className="opl-selection-layer" aria-hidden="true">
                    <div className="opl-selection-layer__border"/>
                    <div className="opl-selection-layer__checkmark"/>
                </div>
                <div
                    className={"g-white1-bg u-padding-all-l    u-text-center " +(disabled?" ":" u-box-shadow--s  u-cursor-pointer")}>
                    <div
                        className={"js-layout-fixer-group-switcher-select" + this.props.id + "  u-display__flex-center"}>
                        <div className="js-height-sensitive-element ">
                            <p className={"h5 u-no-margin" + (disabled?" g-gray3-c ":"")}> {option.description || option.value}</p>
                        </div>
                    </div>
                </div>
            </OraSwitcher>)
    }

    render() {
        if (this.props.options === undefined || !this.props.options.length)
            return null;

        if (this.props.options.length === 1 && this.props.singleOptionRenderer)
            return this.props.singleOptionRenderer(this.props.options[0].description);

        if (this.props.options.length === 1 && this.props.renderSingleOption) {
            return (
                <div
                    className="u-padding-right-m  u-font-bold ">
                    <p className={"u-no-margin "}>{this.props.options[0].description || this.props.options[0].value}</p>
                </div>
            )
        }

        let ulClass = "opl-switcher-list opl-flex-boxes__container";
        return (
            <div
                className={"u-margin-s u-large-margin-l"}
                ref={ref => this.ref = ref}
                id={"switcher_select_" + this.props.id}>
                <ul className={ulClass}>
                    {this.options()}
                </ul>
            </div>
        )
    }
}
OraSwitcherSelect.PropTypes = {
    renderSingleOption: PropTypes.bool,
};
OraSwitcherSelect.defaultProps = {
    renderSingleOption: false,
};
